@keyframes leftToRight {
    from { 
        transform: translateX(-100%);
    }
    to {
        transform: translateX(0);
    }
}
@keyframes rightToLeft {
    from { 
        transform: translateX(100%);
    }
    to {
        display: block;
        transform: translateX(0);
    }
}
