//about me
.me {
    height: 100vh;
    color: $secundary;
    @include flexCenterisation
}
.about-me{
    padding: 80px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-items: center;
    background-color: $primary;
    h2{
        grid-column: span 2;
        justify-self: center;
    }
    .about-txt{
        p{
            margin: 5px 0;
            font-size: 1.2em;
        }
    }
    
}
//folio
.index{
    .folio-bg{
        height: 100vh;
        text-align: center;
        padding: 20px;      
        @include flexCenterisation;
    }
    .portfolio{
        width: 100%;
        border-radius: 20px;
        overflow: hidden;
        background-color: $primary;
        h2{
            color: $secundary;
        }
    }
    .folio-title{
        display: flex;
        
        >div{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 600px;
            transition: all 1s ease-in-out;
            &:nth-of-type(1){
                background: url(../media/img/florian-olivo-unsplash.jpg) center/cover;
            }
            &:nth-of-type(2){
                background: url(../media/img/brett-jordan-unsplash.jpg) center/cover;
            } 
            &:hover{
                width: 300%;
                transition: all 1s ease-in-out;
            }
            h3{
                color: $secundary;
                font-size: 2em;
            }
            .folio-txt{
                background-color: rgba(255, 0, 0, 0.596);
                padding: 40px;
                border-radius: 40px;
            }
        }
    }
}
//compétences
.comp-bg{
    height: 100vh;
    @include flexCenterisation;
    .comp-content{
        width: 50%;
        height: 70%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        justify-items: center;
        gap: 1em;
        background-color: $primary;
        .comp-title{
            grid-column: span 3;
            justify-self: center;
            color: $secundary;
        }
        a{
            &:nth-of-type(7){
                grid-column: span 3;
            }
        }
    }
}
