.header {
    height: 50vh;
    background: url(../media/img/custombg.png) no-repeat center/cover fixed;
}
//------------------------
// spécifique à la homepage
.index{
    header{
        height: 100vh;
    }
    .header-content{
        height: 100vh;
    }
}
//-----------------------
.header-nav-menu{
    background-color: $secundary;
    padding: 5px;
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.header-content{
    width: 100%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .title{
        font-size: 3em;
        color: $secundary;
        overflow: hidden;
        h1{
            margin: 5px;
            transform: translateX(-100%);
            animation: leftToRight forwards 0.5s ;
        }
   
    }
    .txt{
        text-align: center;
        font-size: 2em;
        color: $secundary;
        text-transform: capitalize;
        overflow: hidden;
        p{
            &:nth-of-type(1){
               transform: translateX(100%);
               animation: rightToLeft forwards 0.5s 0.5s ;
            }
            &:nth-of-type(2){
                transform: translateX(-100%);
                animation: leftToRight forwards 0.5s 1s ;
            }
        }
    }
}

