.main-nav{
    display: flex;
    gap: 2em;
    a{
        text-decoration: none;
        color: $primary;
    }
}
.active { 
    background-color: indianred;
}
.logo{
    width: 100px;
    height: 40%;
    img{
        display: block;
        width: 100%;
    }
}
.side-bar{
    height: 100vh;
    width: 350px;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content:space-between ;
    align-items: center;
    gap: 2em;
    background-color: rgba(0, 0, 0, 0.733);
    .side-nav{
        height: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2em;
        a{
            text-decoration: none;
            color: $secundary;
            z-index: 1;
        }   
     }
     .inner-main-nav{
         position: relative;
        text-transform: uppercase;
        font-size: 0.9em;
        transition: 1s all ease-in-out;
        &:after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0;
            height: 2px;
            background-color: $secundary;
            transition: 1s all ease-in-out;
        }
    }
    
    .side-active{
        border-width: 2px;
        font-size: 1.5em;
        transition: 1s all ease-in-out;
        &:after{
            width: 100%;
            transition: 1s all ease-in-out;
        }
    }
}
.hide-nav{
    display: none;
}