//Portfolio
.portfolio{
    background-color: $secundary;
    .exo-copy, .exo-newsletter{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1.5em;
        h3{
            grid-column: span 2;
            justify-self: center;
        }
    }
    article{
        display: flex;
        flex-direction: column;
        .content-txt{
            p{
                display: flex;
                flex-direction: column;
            }
            a{
                text-decoration: none;
                color: red;
            }
        }
    }
    .content-big{
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 80px;
        iframe { 
            background-color: #fff;
        }
        &:before{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 100vh;
            width: 100%;
            background-color: rgba(0, 0, 0, 0.699);
            z-index: -1;
        }
    }
    .show{
        display: block;
    }
    .content-small{
        border: 1px solid $secundary;
        img{
            width: 100%;
        }
        &:hover{
            cursor: pointer;
        }
    }
}
//la petite croix pour fermer la modal
.btn-close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.5;
    &:hover {
        opacity: 1;
      }
      &:before, &:after {
        position: absolute;
        left: 15px;
        content: ' ';
        height: 33px;
        width: 2px;
        background-color: rgb(255, 255, 255);
      }
      &:before {
        transform: rotate(45deg);
      }
      &:after {
        transform: rotate(-45deg);
      }
      
  }