//-----------------------------------------------//
// mixin pour media-querry

$mobile:764px;
$tablet: 990px;

@mixin mobile {
    @media screen and (max-width: $mobile) {
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width: $tablet) {
        @content;
    }
}
//-----------------------------------------------//

//pos absolute center//
@mixin centerisation {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
//flex center//
@mixin flexCenterisation {
    display: flex;
    justify-content: center;
    align-items: center;
}
